import React from 'react';
import {DivElement} from './s-c-header';

const ContributorsButton= () => {
    return(
        <DivElement>
            <i className='fas fa-plus'></i>
            <p>Share</p>
        </DivElement>
    );
}
export default ContributorsButton;


